@import url('https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@300;500;700&family=Red+Hat+Text:wght@300;500;700&display=swap');

*{
  margin: 0;
  box-sizing: border-box;
  font-family: 'Red Hat Text', sans-serif;
  font-weight: 300;
  --title-font: 'Red Hat Display', sans-serif;
  --title-weight: 700;
  
}
body {
  margin: 0;
  font-family: var(--text-font);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-y: auto;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
}

b{
  font-family: var(--title-font);
  font-weight: var(--title-weight);
}
.App{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.dark{
  background-color: #171923;
  color: rgba(255, 255, 255, 0.92) ;
  --primary-color: rgba(255, 255, 255, 0.92) ;
  --secondary-color: #2D3748;
  --primary-bg-color: #171923;
  --secondary-bg-color: rgb(30, 37, 51);
  --third-bg-color: #1A202C;
}
.light{
  background-color: #F7FAFC;
  color: #2D3748;
  --primary-color: #2D3748 ;
  --secondary-color: #EDF2F7;
  --primary-bg-color: #F7FAFC;
  --secondary-bg-color:#EDF2F7;;
  --third-bg-color: #EDF2F7;
}